:root {
  --content-width: 1110px;
  --container-offset: 60px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
}

@font-face {
  font-family: "LabGrotesque";
  src: url("../fonts/LabGrotesque-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "LabGrotesque";
  src: url("../fonts/LabGrotesque-Regular.woff2") format("woff2");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
}

.site-container {
  background-color: #fff;
}

figcaption, figure {
  margin: 0px;
  padding: 0px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.header .header-head {
  background-color: #EFEFEF;
  padding: 17px 0 16px;
  display: flex;
  justify-content: center;
  column-gap: 30px;
}
.header .header-head__item {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #333333;
}
.header .header-main {
  padding: 25px 0 20px;
  display: flex;
  justify-content: space-between;
}
.header .header-main__logo {
  display: flex;
  align-items: center;
  column-gap: 18px;
}
.header .header-main__logo-image {
  width: 80px;
  height: 80px;
}
.header .header-main__logo-text {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 36px;
  line-height: 42px;
  color: #66C05D;
}
.header .header-main__contacts {
  display: flex;
  align-items: center;
  column-gap: 40px;
}
.header .header-main__contacts-number {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.header .header-main__contacts-button {
  padding: 12px 26px 13px 27px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: 2px solid #66C05D;
  border-radius: 40px;
  background-color: inherit;
  transition: 0.3s;
}
.header .header-main__contacts-button:hover {
  transform: scale(1.1);
}
.header .header-bottom {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  column-gap: 20px;
  border-top: 1px solid #EFEFEF;
}
.header .header-bottom__item {
  padding: 10px 20px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
  transition: 0.4s;
}
.header .header-bottom__item:hover {
  background-color: #BFE5BB;
}

.section-promo {
  background-color: #66C05D;
}
.section-promo__container {
  position: relative;
  display: flex;
  align-items: center;
  height: 604px;
  overflow: hidden;
}
.section-promo__describe {
  width: 308px;
}
.section-promo__describe * + * {
  margin-top: 50px;
}
.section-promo__describe-title {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 44px;
  line-height: 56px;
  color: #fff;
}
.section-promo__describe-text {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
}
.section-promo__describe-button {
  padding: 12px 44px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  border: 2px solid #EFEFEF;
  border-radius: 40px;
  background-color: inherit;
  transition: 0.3s;
}
.section-promo__describe-button:hover {
  transform: scale(1.1);
}
.section-promo .background__one {
  position: absolute;
  top: -20%;
  right: 0;
  width: 859px;
  height: 859px;
  border-radius: 50%;
  background-color: #94D38E;
}
.section-promo .background__two {
  position: absolute;
  top: -5%;
  right: 7%;
  width: 677px;
  height: 677px;
  border-radius: 50%;
  background-color: #BFE5BB;
}
.section-promo .background__img {
  position: absolute;
  top: 0;
  right: 10%;
  height: 584px;
  width: 584px;
}

.section-offers {
  padding: 80px 0;
  background-color: #EFEFEF;
}
.section-offers .container {
  position: relative;
}
.section-offers__title {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
}
.section-offers__car {
  position: absolute;
  top: 0;
  right: var(--container-offset);
  width: 189px;
  height: 61px;
}
.section-offers__items {
  display: flex;
  column-gap: 30px;
  margin-top: 38px;
}
.section-offers__item {
  background-color: #fff;
  padding: 25px 30px;
  width: 255px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  text-align: center;
}
.section-offers__item-img {
  width: 125px;
  height: 166px;
}
.section-offers__item-desc {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 115%;
  color: #000000;
}
.section-offers__item-price {
  font-family: "Roboto";
  font-weight: 600;
  font-size: 21px;
  line-height: 100%;
  color: #000000;
  letter-spacing: 0.05em;
}
.section-offers__item-button {
  padding: 9px 40px;
  background-color: #66C05D;
  border-radius: 30px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  letter-spacing: 0.05em;
  transition: 0.3s;
}
.section-offers__item-button:hover {
  transform: scale(1.1);
}

.additional-info {
  padding: 20px 0 133px;
  background-color: #EFEFEF;
}
.additional-info__items {
  display: flex;
  justify-content: space-between;
}
.additional-info .item {
  padding: 35px 40px;
  width: 49%;
  background-color: #66C05D;
}
.additional-info .item__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.additional-info .item__form {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  max-width: 250px;
}
.additional-info .item__title {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  color: #fff;
}
.additional-info .item__text {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
}
.additional-info .item__input input {
  width: 225px;
  border: none;
  outline: none;
  padding: 9px 25px;
  border-radius: 30px;
}
.additional-info .item__input input::placeholder {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #C4C4C4;
}
.additional-info .item__image {
  width: 140px;
  height: 106px;
}
.additional-info .item__button {
  max-width: 170px;
  border: 2px solid #fff;
  border-radius: 30px;
  padding: 9px 28px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #fff;
  transition: 0.3s;
}
.additional-info .item__button:hover {
  transform: scale(1.1);
}

.footer-main {
  padding: 38px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.footer-main__socials * + * {
  margin-left: 16px;
}
.footer-main__social-image {
  width: 45px;
  height: 45px;
}
.footer-main__address {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
}
.footer-main__contacts * + * {
  margin-left: 40px;
}
.footer-main__contacts-number {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.footer-main__contacts-button {
  padding: 12px 26px 13px 27px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: 2px solid #66C05D;
  border-radius: 40px;
  background-color: inherit;
  transition: 0.3s;
}
.footer-main__contacts-button:hover {
  transform: scale(1.1);
}
.footer-bottom {
  padding: 25px 0;
  background-color: #EFEFEF;
}
.footer-bottom_row {
  display: flex;
  justify-content: space-between;
}
.footer-bottom__text {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #828282;
}